<template>
  <div class="index_body" id='app'>
    <!--图片滚动切换 begin -->
    <div class="in_img1">
      <transition name='fade'>
        <img v-for="(img,index) in imgs" :key="index" :src="img" v-if="num==index" />
      </transition>
    </div>
    <!-- 图片滚动切换- end -->

    <div class="con">
      <div class="logo"></div>
      <div class="keshu">
        <div class="keshu_1">
          现有古树名木
        </div>
        <div class="keshu_2">
          共计<span>{{tree_count}}</span>棵
        </div>
      </div>
      <div class="text"></div>
      <router-link :to='{name:"List"}' class="btn_more"></router-link>
      <!-- <a class="btn_more" href="treelist.html"></a> -->
    </div>
    <div class="footer"></div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Home',
  data() {
    return {
      num: 0,
      imgs: [],
      tree_count: 0,
      apiUrl: 'http://djhotel.sh-service.cn'
    }
  },
  created() {
    this.getData();
    this.getImgs();
    this.play();
  },
  methods: {
    getData() {
      this.axios({
        url: '/api/tools/oldtree_ajax.ashx',
        params: {
          action: 'get_tree_count'
        }
      }).then(res => {
        this.tree_count = res.data.treeCount;
      }).catch(res => {
        console.log('请求失败：' + res);
      });
    },
    getImgs() {
      this.axios({
        url: '/api/tools/oldtree_ajax.ashx',
        params: {
          action: 'get_pic',
          number: '230'
        }
      }).then(res => {
        let obj = res.data.result;
        obj.forEach(item => {
          this.imgs.push(this.apiUrl + item.img_url)
        });
      }).catch(res => {
        console.log('请求失败：' + res);
      })
    },
    autoPlay: function () {
      const _self = this;
      _self.num++;
      if (_self.num == this.imgs.length) {
        _self.num = 0;
      }
    },
    play: function () {
      setInterval(this.autoPlay, 5000);
    }
  }
}
</script>

