<template>
  <div class="index_con">
    <div class="con">
      <div class="treeimg1">
        <div class="logo1"></div>
        <transition name='fade'>
          <img v-for="(img,index) in imgs" :key="index" :src="img" v-if="num==index" />
        </transition>
        <div class="name">{{info.name}}</div>
      </div>

      <div class="treejj_con">
        <ul class="kslist">
          <li>
            古树科属：<i>{{info.subjects}}</i>
          </li>
          <li>
            古树树高：<i>{{info.treeheight}}(M)</i>
          </li>
          <li>
            古树年龄：<i>{{info.treeage}}</i>
          </li>
          <li>
            古树蓬径: <i>{{info.weight}}(CM)</i>
          </li>
          <li>
            古树蓬径: <i>{{info.treebust}}(CM)</i>
          </li>
          <li>
            古树产地：<i>{{info.address}}</i>
          </li>
          <div class="clearfix"></div>
        </ul>

        <div class="conjj">
          <div class="title"></div>
          {{info.introduced | ReName}}
          <!-- {{info.introduced}} -->
        </div>
      </div>

      <div class="clearfix"></div>
    </div>
  </div>
</template>

<script>
import ReName from '@/filters/ReName'

export default {
  name: 'Detail',
  //   props: ['id'],
  filters: {
    ReName
  },
  data() {
    return {
      info: [],
      num: 0,
      imgs: [],
      apiUrl: 'http://djhotel.sh-service.cn'
    }
  },
  created() {
    // console.log(this.$route.params.id)
    this.getData();
    this.play();
  },
  methods: {
    getData() {
      this.axios({
        url: '/api/tools/oldtree_ajax.ashx',
        params: {
          action: 'get_oldtree',
          number: this.$route.params.id
        }
      }).then(res => {
        // console.log(res.data);
        this.info = res.data.result;
        this.imgs = res.data.result.imgurl;
      }).catch(res => {
        console.log('请求失败：' + res.status);
      });
    },
    //num递增，num等于图片总数时回到第一张，其递增有下面的定时器实现
    autoPlay: function () {
      const _self = this;
      _self.num++;
      if (_self.num == this.imgs.length) {
        _self.num = 0;
      }
    },
    //每隔一秒执行autoPlay，使得num递增
    play: function () {
      setInterval(this.autoPlay, 5000);
    }
  }
}
</script>

